.timeline {
  background-color: var(--white);
  height: max-content;
  width: 600px;
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
  -webkit-box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
}

.icon {
  color: var(--blue);
  font-size: 1rem;
  cursor: pointer;
}

.icon2 {
  font-size: 1.5rem;
}

.img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 10px;
}

img {
  object-fit: cover;
  width: 100%;
}

.timelineTop {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.infoContent {
  display: flex;
  align-items: center;
  justify-content: center;
}

.name h4 {
  color: var(--blue);
  font-weight: bold;
}

.name span {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.name span p {
  margin-right: 10px;
}

.desc {
  margin: 1rem;
  color: var(--blue);
}

.photo {
  width: 100%;
  height: 320px;
  border-radius: 0.7rem;
  overflow: hidden;
}

.photo img {
  transition: 0.3s all ease-in;
  cursor: pointer;
}

.photo:hover img {
  transform: scale(1.04);
}

.likes {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.5rem 0;
  font-size: 0.9rem;
}

.likes div {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--blue);
}

.likes div .icon {
  width: 15px;
  margin: 0;
}
.likes div .icon2 {
  margin-right: 5px;
}

.commentCount {
  display: flex;
  gap: 0.5rem;
}

.click {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 0;
}

.click span {
  flex-basis: 33%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
  width: 8rem;
  border-radius: 0.3rem;
  cursor: pointer;
}

.click span:hover {
  background-color: var(--cyan);
  color: var(--white);
}
.click span .icon {
  font-size: 1.2rem;
  margin-right: 0.3rem;
}

.click span:hover .icon {
  color: var(--white);
}

.comment {
  margin: 1rem 0;
}

.commentTop {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.commentDetails {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.commentName {
  background-color: var(--blue);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  color: var(--white);
  margin-bottom: 0.3rem;
}

.commentName h5 {
  font-size: 1rem;
  margin-bottom: 0.2rem;
}

.commentName p {
  font-size: 0.8rem;
}

.commentLikes {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  cursor: pointer;
  margin: 0.3rem 0;
}

.commentLikes h6 {
  font-weight: bold;
  font-size: 0.8rem;
}

.light {
  font-weight: light;
}

.commentBottom {
  display: flex;
  align-items: center;
  width: 100%;
}

.input {
  position: relative;
  width: 90%;
}

.input input {
  position: relative;
  border: none;
  outline: none;
  background-color: #ccc;
  padding: 0.5rem 1rem;
  width: 100%;
  border-radius: 1rem;
}

.input .icon {
  position: absolute;
  right: 10px;
  top: 20%;
  font-size: 1.2rem;
}
