.pagesTop {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagesTop h3 {
  margin: 0;
  font-size: 1rem;
  color: var(--blue);
}

.pagesLogoBox {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0.6rem 0;
  cursor: pointer;
  padding: 0.4rem;
  border-radius: 0.5rem;
}

.pagesLogoBox:hover {
  background-color: var(--cyan);
}

.pagesLogoBox:hover h4,
.pagesLogoBox:hover .pageLogo {
  color: var(--white);
}

.pagesLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  background-color: var(--blue);
  border-radius: 50%;
  color: var(--white);
  cursor: pointer;
}

.pagesLogoBox h4 {
  color: var(--blue);
  cursor: pointer;
}

ul {
  list-style: none;
}

ul li {
  display: flex;
  align-items: center;
  margin: 0.8rem 0;
  cursor: pointer;
  padding: 0.4rem 0.8rem;
  border-radius: 0.5rem;
}

ul li:hover {
  background-color: var(--cyan);
}

ul .icon {
  font-size: 1.2rem;
  margin-right: 0.5rem;
}

ul li:hover .icon,
ul li:hover span {
  color: var(--white);
}
