.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.4rem 0.8rem;
  border-radius: 0.5rem;
}

/* .title:hover{
    color: var(--white);
   background-color: var(--cyan);
} */

.title h3 {
  margin: 0;
}

.title span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin: 0 0.3rem;
}

.title span:hover {
  background-color: var(--cyan);
}

.title span:hover .icon {
  color: var(--white);
}

.online {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0;
}

.onlineImg {
  position: relative;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.online img {
  width: 100%;
  object-fit: cover;
}

.online span {
  position: absolute;
  left: 40px;
  bottom: 10px;
  height: 12px;
  width: 12px;
  border: 2px solid var(--white);
  border-radius: 50%;
  background-color: red;
}

.online h4 {
  color: var(--blue);
}

.online:hover h4 {
  color: var(--white);
}

.group .img {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--blue);
  color: var(--white);
  cursor: pointer;
}
.group > div {
  display: flex;
  align-items: center;
  margin: 1rem 0;
  padding: 0.4rem 0.6rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.group p {
  color: var(--blue);
}

.group > div:hover {
  background-color: var(--cyan);
}

.group > div:hover p {
  color: var(--white);
}
