.sideBar {
  flex: 3;
  height: calc(100vh - 60px);
  padding: 20px;
  overflow-y: scroll;
  position: sticky;
  top: 60px;
}

::-webkit-scrollbar {
  display: none;
}

.profileName{
  font-weight: 700;
}

.sideLists {
  margin: 0;
  padding: 0;
  list-style: none;
}

.listItem {
  height: 3rem;
  padding: 0.8rem;
  border-radius: 0.4rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0.2rem 0;
  cursor: pointer;
}

.listItem:hover {
  background-color: var(--cyan);
}

.listItem:hover span {
  color: var(--white);
  font-weight: 500;
}

.listItem:hover .sideIcons {
  color: var(--white);
}

.profile {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.6);
  margin-right: 1rem;
}

.profile img {
  width: 100%;
  object-fit: cover;
}

.sideIcons {
  color: var(--blue);
  margin-right: 1rem;
}


.listItem span {
  font-weight: 500;
  color: var(--blue);
}

hr {
  height: 0.2rem;
  background-color: var(--blue);
  margin: 1rem 0;
}

.seeMoreBtn {
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.4rem 0;
  color: var(--blue);
  margin-left: 15px;
  background-color: transparent;
  cursor: pointer;
}

h3 {
  margin-top: 1rem;
  color: var(--blue);
  margin-left: 20px;
}

.short p {
  color: var(--blue);
  font-weight: 700;
  font-size: 1rem;
}

.listItem:hover p{
  color: var(--white);
}

a {
  text-decoration: none;
  position: fixed;
  left: 0;
  bottom: 0;
  font-size: 0.9rem;
  background-color: var(--white);
  padding: 0.2rem;
}
