.topBar {
  background-color: var(--blue);
  display: flex;
  height: 60px;
  padding: 0 1rem;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
}

.top-left {
  flex: 25%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.top-left h1 {
  color: var(--white);
  font-size: 2rem;
  font-weight: 700;
  margin-right: 1rem;
  background-color: var(--cyan);
  padding: 0.4rem;
  border-radius: 50%;
}

.top-left .search {
  position: relative;
  width: 100%;
  height: 50%;
}

.top-left input {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: var(--cyan);
  padding: 0.7rem 2rem;
  outline: none;
  border: none;
  border-radius: 2rem;
  color: var(--white);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.searchIcon {
  position: absolute;
  top: 20%;
  left: 10px;
  color: var(--white);
  z-index: 10;
}

.top-left input::placeholder {
  color: var(--white);
  margin-left: 70px;
  padding-left: 10px;
}

.top-center {
  flex: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.top-center div {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1.6rem;
  cursor: pointer;
  padding: 0.4rem 1.4rem;
  border-radius: 0.4rem;
}

.top-center div .icons {
  font-size: 1.6rem;
  color: var(--white);
}

.top-center div span {
  position: absolute;
  bottom: -50px;
 background-color: var(--cyan);
  padding: 0.4rem;
  border-radius: 0.3rem;
  font-size: 0.8rem;
  display: none;
}

.top-center div:hover {
 background-color: var(--cyan);
}

.top-center div:hover span {
  color: var(--white);
  display: block;
  transition: 0.3s all ease-in;
}

.top-right {
  flex: 25%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.top-right div {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 0.2rem;
  cursor: pointer;
}

.top-right div img {
  width: 100%;
  object-fit: cover;
}

.top-right .icons {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: var(--cyan);
  color: var(--white);
}

.top-right .icons:hover {
  border: 1px solid var(--white);
  background-color: var(--blue);
}

.top-right span {
  position: absolute;
  bottom: -50px;
  background-color: var(--cyan);
  color: var(--white);
  padding: 0.4rem;
  border-radius: 0.3rem;
  font-size: 0.8rem;
  display: none;
}

.top-right div:hover span {
  display: block;
}
