.reelsContainer {
  background-color: var(--white);
  height: 250px;
  width: 600px;
  margin: 1rem 0;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
  -webkit-box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
}

.reels {
  height: 100%;
  width: 100%;
}

.top {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 25%;
  padding: 1rem;
  border-bottom: 2px solid var(--blue);
}

.top button {
  border: none;
  outline: none;
  color: var(--blue);
  font-size: 1.1rem;
  font-weight: 600;
  padding: 0.6rem 3rem;
  background-color: transparent;
  cursor: pointer;
}

.top button:hover {
  border-radius: 0.6rem;
 background-color: var(--cyan);
  color: var(--white);
}

.top button.active {
  color: var(--white);
  border-radius: 0.6rem;
  background-color: var(--cyan);
}

.bottom {
  height: 75%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0.5rem 1rem;
  flex-flow: row;
}

.reelPix::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.4));
}


.reelPix:hover img {
  transform: scale(1.1);
}

.pp {
  position: absolute;
  top: 10px;
  left: 5px;
  height: 40px;
  width: 40px;
  border: 2px solid var(--white);
  border-radius: 50%;
  overflow: hidden;
}

.reelPix p {
  position: absolute;
  bottom: 5px;
  font-size: 12px;
  color: var(--white);
  font-weight: 500;
  width: 100%;
  z-index: 100;
}

/* Swiper */
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  position: relative;
  text-align: center;
  font-size: 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  border-radius: .5rem;
  overflow: hidden;
  cursor: pointer;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
