.upload {
  height: 120px;
  width: 600px;
  border-radius: 1rem;
  box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
  -webkit-box-shadow: 0 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin-bottom: 1rem;
}

.uploadPost{
  height: 100%;
}

.uploadTop {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.4rem;
  height: 40%;
}

.uploadTop div {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 20px;
}

.uploadTop div img {
  width: 100%;
  object-fit: cover;
}

.uploadTop input {
  width: 100%;
  outline: none;
  padding: 0.6rem 1rem;
  border: none;
  border-radius: 1rem;
  background-color: rgb(243, 243, 243);
}

hr {
  height: 3px;
  opacity: 0.3;
  background-color: var(--blue);
  margin: 0;
}

.uploadBottom {
  height: 60%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.uploadBottom div {
  display: flex;
  flex-basis: 26%;
  justify-content: center;
  align-items: center;
  padding: 0.3rem 0.8rem;
  border-radius: 0.3rem;
  cursor: pointer;
}

.uploadBottom div:hover {
  background-color: var(--cyan);
  
}

.uploadBottom div:hover span{
  color: var(--white);
}

.uploadIcons {
  margin-right: 10px;
  color: var(--blue);
}

.uploadBottom div:hover .uploadIcons{
   color: var(--white);
}
